.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background: no-repeat url(../../images/star-active.svg) center;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--0 :nth-child(n+1),
.stars--1 :nth-child(n+2),
.stars--2 :nth-child(n+3),
.stars--3 :nth-child(n+4),
.stars--4 :nth-child(n+5) {
  background-image: url(../../images/star.svg);
}
